import { Component, OnInit, TemplateRef } from '@angular/core';
import { ApiService } from 'src/app/shared/services/api.service';
// import { ApiResponse } from 'src/app/shared/models/api-response.model';
import { BsModalService, BsModalRef, ModalDirective } from 'ngx-bootstrap/modal';
import { LoginResponse } from 'src/app/shared/models/login-response.model';
import { Md5 } from 'ts-md5/dist/md5';
import { catchError, retry, first } from 'rxjs/operators';
import { AuthenticationService } from 'src/app/shared/services/authentication.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
    selector: 'app-login',
    templateUrl: './page-login.component.html',
    styleUrls: ['./page-login.component.scss'],
    providers: [
        ApiService,
        AuthenticationService
    ]
})
export class PageLoginComponent implements OnInit{
    error: any;
    user: LoginResponse;
    email: string;
    password: string;
    message: string;
    noUser = false;
    forgetPasswordModalRef: BsModalRef;
    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private authenService: AuthenticationService,
        private apiservice: ApiService,
        private modalService: BsModalService,
    ) { }

    ngOnInit(): void {

    }

    login() {
        const md5 = new Md5();
        const encryptedPass = md5.appendStr(this.password).end();

        this.authenService.login(this.email, encryptedPass as string).pipe(first())
            .subscribe(
            data => {
                console.log(data);
                // this.router.navigate([this.returnUrl]);
                this.router.navigate(['/m']);
            },
            err => {
                // TODO: Check header and message
                this.error = err;
                // this.loading = false;
            });
    }

    public forgetPasswordModal(template: TemplateRef<any>) {
        this.forgetPasswordModalRef = this.modalService.show(template);
    }

    forgetPassword() {
        this.apiservice.forgetpassword(this.email).subscribe(
            (response: any) => {
                //TODO
            },
            err => this.error = err
        );
    }
}
