import { Component } from '@angular/core';
import { ApiService } from 'src/app/shared/services/api.service';
import { AuthenticationService } from 'src/app/shared/services/authentication.service';
import { LoginResponse } from 'src/app/shared/models/login-response.model';
import { Md5 } from 'ts-md5';

@Component({
    selector: 'app-page-password',
    templateUrl: './page-password.component.html',
    styleUrls: ['./page-password.component.sass'],
    providers: [
        ApiService,
        AuthenticationService
    ]
})
export class PagePasswordComponent {
    error: any;
    user: LoginResponse;
    current_password: string;
    new_password: string;
    re_new_password: string;
    // Form help
    formError: any = {};
    constructor(
        private authenService: AuthenticationService,
        private apiservice: ApiService,
    ) {
        this.user = this.authenService.currentUserValue;
    }

    async checkFormError() {
        let isError = false;

        // current_password
        if (!this.current_password) {
            this.formError.curPasswordError = 'กรุณากรอกรหัสผ่านปัจจุบัน'; isError = true;
        } else {
            delete this.formError.curPasswordError;
        }

        // new_password
        if (!this.new_password) {
            this.formError.newPasswordError = 'กรุณากรอกรหัสผ่านใหม่'; isError = true;
        } else {
            delete this.formError.newPasswordError;
        }

        // re_new_password
        if (!this.re_new_password) {
          this.formError.reNewPasswordError = 'กรุณากรอกรหัสผ่านใหม่'; isError = true;
        } else if (this.new_password !== this.re_new_password) {
          this.formError.reNewPasswordError = 'รหัสผ่านไม่ตรงกัน'; isError = true;
        } else {
          delete this.formError.reNewPasswordError;
        }

        return isError;
    }

    async resetPassword() {
        const error = await this.checkFormError();
            if (error) {
            window.scroll(0, 0);
            return;
        }

        const md5 = new Md5();
        const encryptedCurPass = md5.appendStr(this.current_password).end();
        const encryptedNewPass = md5.appendStr(this.new_password).end();

        this.apiservice.resetpassword(this.user.authority_info.Email, encryptedCurPass as string, encryptedNewPass as string).subscribe(
            (response: LoginResponse) => {
                //TODO
            },
            err => this.error = err
        );
    }
}
