import { Component } from '@angular/core';
import { AuthenticationService } from 'src/app/shared/services/authentication.service';
import { LoginResponse } from 'src/app/shared/models/login-response.model';
import { ApiService } from 'src/app/shared/services/api.service';
import { ApiResponse } from 'src/app/shared/models/api-response.model';
import { Citizen } from 'src/app/shared/models/citizen.model';
import * as globals from 'src/globals';

@Component({
    selector: 'app-page-profile',
    templateUrl: './page-profile.component.html',
    styleUrls: ['./page-profile.component.sass'],
    providers: [
        AuthenticationService,
        ApiService
    ]
})
export class PageProfileComponent {
    user: LoginResponse;
    error: any;
    citizen: Citizen = new Citizen();
    // Form help
    formError: any = {};

    constructor(
        private authenService: AuthenticationService,
        private apiService: ApiService,
    ) {
        this.user = this.authenService.currentUserValue;
        console.log(this.user);
        this.fetchCitizenInfo();
    }

    fetchCitizenInfo() {
        this.apiService.getcitizeninfo(this.user.authority_info.CitizenId).subscribe(
            (response: ApiResponse<Citizen>) => {
                this.citizen = response.data;
                console.log("--------------------------");
                console.log(this.citizen);
            },
            err => this.error = err
        );
    }

    async checkFormError() {
        let isError = false;

        // FName
        if (!this.citizen.FName) {
            this.formError.FnameError = 'กรุณากรอกชื่อ'; isError = true;
        } else {
            delete this.formError.FnameError;
        }

        // LName
        if (!this.citizen.LName) {
            this.formError.LnameError = 'กรุณากรอกนามสกุล'; isError = true;
        } else {
            delete this.formError.LnameError;
        }

        // IdCard
        if (!this.citizen.IdCard) {
            this.formError.citizenIdError = 'กรุณากรอกเลขบัตประชาชน'; isError = true;
        } else if (!globals.onlyNumericCharacter(this.citizen.IdCard)) {
            this.formError.citizenIdError = 'เลขบัตประชาชนต้องเป็นตัวเลขเท่านั้น'; isError = true;
        } else {
            delete this.formError.citizenIdError;
        }

        // Email
        if (!this.citizen.Email) {
            this.formError.emailError = 'กรุณากรอกอีเมลล์'; isError = true;
        } else {
            delete this.formError.emailError;
        }

        // tel
        if (!this.citizen.Tel || this.citizen.Tel.length < 9) {
            this.formError.telError = 'กรุณากรอกเบอร์โทรศัพท์ให้ครบ'; isError = true;
          } else if (!globals.onlyNumericCharacter(this.citizen.Tel)) {
            this.formError.telError = 'เบอร์โทรศัพท์ต้องเป็นตัวเลขเท่านั้น'; isError = true;
          } else {
            delete this.formError.telError;
          }

        return isError;
    }

    async editProfile() {
        const error = await this.checkFormError();
            if (error) {
            window.scroll(0, 0);
            return;
        }

        this.apiService.editcitizeninfo(
            this.citizen.CitizenId,
            this.citizen.FName,
            this.citizen.LName,
            this.citizen.Tel).subscribe(
            (response: ApiResponse<any>) => {
                console.log(response);
            },
            err => this.error = err
        );
    }
}
