import { Component, ViewEncapsulation, TemplateRef, ComponentFactoryResolver } from '@angular/core';
import { BsModalService, BsModalRef, ModalDirective } from 'ngx-bootstrap/modal';
// import { DialogService, BuiltInOptions } from 'ngx-bootstrap-modal';
// import { Address } from '../../../../shared/interfaces/address';
import { addresses } from '../../../../../data/account-addresses';
import { ApiService } from 'src/app/shared/services/api.service';
import { ApiResponse } from 'src/app/shared/models/api-response.model';
import { CitizenAddress } from 'src/app/shared/models/citizen-address.model';
import { AuthenticationService } from 'src/app/shared/services/authentication.service';
import * as globals from 'src/globals';

@Component({
    selector: 'app-page-addresses-list',
    templateUrl: './page-addresses-list.component.html',
    styleUrls: ['./page-addresses-list.component.sass'],
    providers: [
        AuthenticationService,
        ApiService,
        // DialogService
    ],
    encapsulation: ViewEncapsulation.None
})
export class PageAddressesListComponent {
    error: any;
    addresses: CitizenAddress[];
    deleteAddressModalRef: BsModalRef;
    addAddressModalRef: BsModalRef;
    public toDeleteId: number;
    toAddress: CitizenAddress = new CitizenAddress() ;

     // Form help
    formError: any = {};

    constructor(
        private authenService: AuthenticationService,
        private modalService: BsModalService,
        private apiService: ApiService,
        // private dialogService: DialogService
    ) {
        this.fetchAddress();
    }

    fetchAddress() {
        const citizenId = this.authenService.currentUserValue.authority_info.CitizenId;
        this.apiService.getcitizenaddress(citizenId).subscribe(
            (response: ApiResponse<Array<CitizenAddress>>) => {
                this.addresses = [...response.data];
            },
            err => this.error = err
        );
    }

    public showAddressModal(template: TemplateRef<any>, item) {
        this.toAddress = Object.assign({}, item);
        this.addAddressModalRef = this.modalService.show(template);
    }

    public addAddressModal(template: TemplateRef<any>) {
        this.formError = {};
        this.toAddress = new CitizenAddress();
        this.addAddressModalRef = this.modalService.show(template);
    }

    public openConfirmDeleteModal(template: TemplateRef<any>, addressId) {
        this.toDeleteId = addressId;
        this.deleteAddressModalRef = this.modalService.show(template);
    }

    async checkFormError() {
        let isError = false;

        // name
        if (!this.toAddress.name) {
            this.formError.nameError = 'กรุณากรอกชื่อผู้รับ'; isError = true;
        } else {
            delete this.formError.nameError;
        }

        // address
        if (!this.toAddress.address) {
            this.formError.addressError = 'กรุณากรอกที่อยู่ผู้รับ'; isError = true;
        } else {
            delete this.formError.addressError;
        }

        // tel
        if (!this.toAddress.tel || this.toAddress.tel.length < 9) {
          this.formError.telError = 'กรุณากรอกเบอร์โทรศัพท์ให้ครบ'; isError = true;
        } else if (!globals.onlyNumericCharacter(this.toAddress.tel)) {
          this.formError.telError = 'เบอร์โทรศัพท์ต้องเป็นตัวเลขเท่านั้น'; isError = true;
        } else {
          delete this.formError.telError;
        }

        return isError;
    }

    async addOrEditAddress() {

        const error = await this.checkFormError();
        if (error) {
        window.scroll(0, 0);
        return;
        }

        this.addAddressModalRef.hide();
        if (this.toAddress.address_id) {
          // Edit
          this.apiService.updatecitizenaddress(this.toAddress.address_id, this.toAddress.name, this.toAddress.address, this.toAddress.tel)
            .subscribe((response: ApiResponse<CitizenAddress>) => {
              this.toAddress = new CitizenAddress();
            //   this.dialogService.confirm('สำเร็จ', 'แก้ไขข้อมูลประเภทตลาดสำเร็จ', <BuiltInOptions>{
            //     type: 'prompt',
            //     showCancelButton: false,
            //     icon: 'success',
            //     confirmButtonText: 'ตกลง'
            //   }).then((result: boolean) => {
            //     // TODO: หลังจากกดปุ่มตกลงให้ทำอะไร
            //   });
              this.fetchAddress();
            });
        } else {
          // Add
          const citizenId = this.authenService.currentUserValue.authority_info.CitizenId;
          this.apiService.addcitizenaddress(citizenId, this.toAddress.name, this.toAddress.address, this.toAddress.tel)
            .subscribe((response: ApiResponse<CitizenAddress>) => {
              this.toAddress = new CitizenAddress();
            //   this.dialogService.confirm('สำเร็จ', 'เพิ่มข้อมูลประเภทตลาดสำเร็จ', <BuiltInOptions>{
            //     type: 'prompt',
            //     showCancelButton: false,
            //     icon: 'success',
            //     confirmButtonText: 'ตกลง'
            //   }).then((result: boolean) => {
            //     // TODO: หลังจากกดปุ่มตกลงให้ทำอะไร
            //   });
              this.fetchAddress();
            });
        }
    }

    public deleteAddress() {
        this.apiService.removecitizenaddress(this.toDeleteId)
          .subscribe((response: any) => {
            // this.dialogService.confirm('สำเร็จ', 'ลบข้อมูลสำเร็จ', <BuiltInOptions>{
            //   type: 'alert',
            //   showCancelButton: false,
            //   icon: 'success',
            //   confirmButtonText: 'ตกลง'
            // }).then((result: boolean) => {
            //   // TODO: หลังจากกดปุ่มตกลงให้ทำอะไร
            // });
            this.fetchAddress();
            this.deleteAddressModalRef.hide();
          });
    }

    public setDefaultAdress(id: number) {
        this.apiService.setdefaultcitizenaddress(id).subscribe((response: ApiResponse<CitizenAddress>) => {
            this.fetchAddress();
        });
    }


}
