import { Component, TemplateRef } from '@angular/core';
import { orders } from '../../../../../data/account-orders';
import { Address } from '../../../../shared/interfaces/address';
import { addresses } from '../../../../../data/account-addresses';
import { AuthenticationService } from 'src/app/shared/services/authentication.service';
import { ApiService } from 'src/app/shared/services/api.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap';
import { ApiResponse } from 'src/app/shared/models/api-response.model';
import { CitizenAddress } from 'src/app/shared/models/citizen-address.model';
import * as globals from 'src/globals';
import { LoginResponse } from 'src/app/shared/models/login-response.model';
import { Order } from 'src/app/shared/models/order.model';

@Component({
    selector: 'app-page-dashboard',
    templateUrl: './page-dashboard.component.html',
    styleUrls: ['./page-dashboard.component.sass'],
    providers: [
        AuthenticationService,
        ApiService,
    ],
})
export class PageDashboardComponent {
    // address: Address = addresses[0];
    error: any;
    defaultAddress: CitizenAddress = new CitizenAddress() ;
    allAddress: CitizenAddress[] = [];
    allOrder: Order[] = [];
    editAddressModalRef: BsModalRef;
    // orders: Order[] = orders.slice(0, 3);
    user: LoginResponse;

    // Form help
    formError: any = {};

    constructor(
        private authenService: AuthenticationService,
        private modalService: BsModalService,
        private apiService: ApiService,
    ) {
        const citizenId = this.authenService.currentUserValue.authority_info.CitizenId;
        this.user = this.authenService.currentUserValue;
        this.fetchAddress();
    }

    fetchAddress() {
        const citizenId = this.authenService.currentUserValue.authority_info.CitizenId;
        this.apiService.getcitizenaddress(citizenId).subscribe(
            (response: ApiResponse<Array<CitizenAddress>>) => {
                this.allAddress = [...response.data];
                this.defaultAddress = this.allAddress.filter(a => a.is_default)[0];
            },
            err => this.error = err
        );


    }

    fetchAllOrder() {
        this.apiService.getOrderList(this.user.authority_info.CitizenId).subscribe(
            (response: ApiResponse<Array<Order>>) => {
                this.allOrder = [...response.data];
            }
        );
    }

    getImageUrl(uri: string) {
        if (!uri) {
            return '/assets/images/na.jpg';
        }
        return 'https://market.aniccom.com' + uri;
    }

    public showAddressModal(template: TemplateRef<any>, item) {
        this.defaultAddress = Object.assign({}, item);
        this.editAddressModalRef = this.modalService.show(template);
    }

    async checkFormError() {
        let isError = false;

        // name
        if (!this.defaultAddress.name) {
            this.formError.nameError = 'กรุณากรอกชื่อผู้รับ'; isError = true;
        } else {
            delete this.formError.nameError;
        }

        // address
        if (!this.defaultAddress.address) {
            this.formError.addressError = 'กรุณากรอกที่อยู่ผู้รับ'; isError = true;
        } else {
            delete this.formError.addressError;
        }

        // tel
        if (!this.defaultAddress.tel || this.defaultAddress.tel.length < 9) {
          this.formError.telError = 'กรุณากรอกเบอร์โทรศัพท์ให้ครบ'; isError = true;
        } else if (!globals.onlyNumericCharacter(this.defaultAddress.tel)) {
          this.formError.telError = 'เบอร์โทรศัพท์ต้องเป็นตัวเลขเท่านั้น'; isError = true;
        } else {
          delete this.formError.telError;
        }

        return isError;
    }

    async editAddress() {
        const error = await this.checkFormError();
            if (error) {
            window.scroll(0, 0);
            return;
        }

        this.editAddressModalRef.hide();
        this.apiService.updatecitizenaddress(
                        this.defaultAddress.address_id,
                        this.defaultAddress.name,
                        this.defaultAddress.address,
                        this.defaultAddress.tel).subscribe(
            (response: ApiResponse<CitizenAddress>) => {
                this.defaultAddress = new CitizenAddress();
                this.fetchAddress();
        },
            err => this.error = err
        );
    }
}
